$iosevka-font: "Iosevka Web", sans-serif;
* {
    background-color: rgb(39, 40, 34);
    color: azure;
    box-sizing: border-box;
    font-family:$iosevka-font;
  }
  
  .container {
    width: 100%;
  }
  
  .left, .right {
    display: inline-block;
    width: 50%;
    margin: 0px;
    padding:0px;
  }
  
  .left {
    float: left;
  }
  
  .right {
    float: right;
  }
  
  #editor {
    width: 100%;
    height: 100vh;
    outline: none;
    border: none;
    overflow-y: scroll;
  }
  
  #preview {
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
   
  }
  
  code {
    background-color: #333;
    color: red;
    padding: 2px 4px;
    border-radius: 5px;
  }
  
  pre {
    background-color: #333;
    padding: 10px;
  
    > code {
      background-color: #333;
      color: azure;
    }
  }
  
  a {
    color: blue;
  }
  
  blockquote {
    border-left: 3px solid whitesmoke;
    padding-left: 1rem;
  }
  
  table, td, th {
    border: 1px solid;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
  }
  
  
  ::-webkit-scrollbar {
    width: 10px;
    height: 10px;
  }
  
  
  ::-webkit-scrollbar-thumb {
    background-color: #888;
    border-radius: 5px;
  }
  
  
  ::-webkit-scrollbar-track {
    background-color: rgb(47, 43, 43);
    border-radius: 5px;
  }
  